<template>
    <v-container style="height: 100%; position: relative; width: 100%" align="center">

        <v-row style="height: 100%">
            <v-col cols="12"
                   sm="6" offset-sm="3"
                   md="4" offset-md="4"
                   style="height: 100%;"
                   align="center">

                    <v-avatar class="centered-avatar" style="width: 35%; padding-top: 35%; vertical-align: top; position: relative;">
                        <img
                                style="width: 100%; position: absolute; top: 0; height: 100%; object-position: center; object-fit: cover;"
                                class="white--text align-end"
                                :aspect-ratio="1"
                                :src="profileImage"
                        >
                    </v-avatar>
            </v-col>
        </v-row>


        <h3 align="center" style="margin-bottom: 10px">@{{username}}</h3>

        <p align="center"><i>"{{description}}"</i></p>

        <p align="center" class="text-with-icon" style="font-size:14px;"><v-icon style="display: inline;  vertical-align: top; font-size:18px;">mdi-cake-variant</v-icon> {{birthDate}}</p>


        <hr>
        <br>

        <v-row style="height: 100%;">
            <h4 style="display: inline-block">Idiomas maternos</h4>
            <v-spacer></v-spacer>
        </v-row>

        <v-row style="height: 100%; margin-bottom: 10px">
            <div class="message received" style="width: 100%;">
                <div v-for="language in known_languages" style="display: inline;">
                    <v-avatar>
                        <img
                                style="width: 65%; height: 65%;  object-fit: cover;"
                                class="white--text align-end"
                                :aspect-ratio="1"
                                :src="createPathToFlagImage(language)"
                        >
                    </v-avatar>
                </div>
                <v-spacer></v-spacer>
            </div>
        </v-row>


        <v-row style="height: 100%;">
            <v-spacer></v-spacer>
            <div>
                <h4 style="display: inline-block" align="right">Idiomas a practicar</h4>
            </div>
        </v-row>

        <v-row style="height: 100%;">
            <v-spacer></v-spacer>
            <div class="message sent" style="width: 100%">
                <p style="display: inline;"> </p>
                <v-spacer style="display: inline;"></v-spacer>
                <div v-for="language in languages_to_learn" style="display: inline; float: right">
                    <v-avatar>
                        <img
                                style="width: 65%; height: 65%;  object-fit: cover;"
                                class="white--text align-end"
                                :aspect-ratio="1"
                                :src="createPathToFlagImage(language)"
                        >
                    </v-avatar>
                </div>
            </div>
        </v-row>


        <v-snackbar
                v-model="snackbar"
                :timeout="4000"
        >
            {{ snackbarText }}
            <v-btn
                    color="blue"
                    text
                    @click="snackbar = false"
            >
                Cerrar
            </v-btn>
        </v-snackbar>
    </v-container>
</template>

<script>
    import * as firebase from "firebase";
    const userImage = '../img/defaultUserImages/defaultUserImage.svg';
    const defaultUsername = 'mario';

    export default {
        name: "PublicProfile",
        data: ()=> ({
            profileImage: userImage,
            username: defaultUsername,
            known_languages: null,
            languages_to_learn: null,
            snackbar: false,
            birthDate: null,
            snackbarText: '',
            description: '',
        }),
        methods: {
            createPathToFlagImage: function (country) {
                return "../img/flags/" + country + ".svg";
            },
            getUserInfo(){
                let firestoreDB = firebase.firestore();
                let userID = firebase.auth().currentUser.uid;
                let self = this;

                firestoreDB.collection("Users").doc(userID).get().then(function(doc) {
                    if (doc.exists) {
                        self.known_languages = doc.data().known_languages;
                        self.languages_to_learn = doc.data().languages_to_learn;
                        self.description = doc.data().description;
                        self.birthDate = self.formatDateInTextMonth(doc.data().birthDate);
                        self.profileImage = doc.data().profileImage;
                    } else {
                        console.warn("No such document!");
                    }
                }).catch(function(error) {
                    console.warn("Error getting document:", error);
                    self.snackbarText = 'Parece que ha habido un problema. Por favor, asegúrese de estar conectado a Internet.'
                    self.snackbar = true
                });
            },
            formatDateInTextMonth(date){
                let months = new Array ("enero","febrero","marzo","abril","mayo","junio","julio","agosto","septiembre","octubre","noviembre","diciembre");
                let d = new Date(date);
                return d.getDate() + " de " + months[d.getMonth()] + " de " + d.getFullYear();
            }
        },
        mounted() {
            this.getUserInfo();
        }
    }
</script>

<style scoped>
    /* Color de fondo de los mensajes: lightskyblue*/


    *, *:before, *:after {
        box-sizing: inherit;
    }

    html {
        box-sizing: border-box;
        height: 100%;
        margin: 0;
        padding: 0;
    }

    body {
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        font-family: "Roboto", sans-serif;
        margin: 0;
        padding: 0;
        height: 100%;
        background:url(https://i.ibb.co/q9mygMq/background.jpg);
        background-repeat: repeat;
        background-size:cover;
    }

    .page {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .marvel-device .screen {
        text-align: left;
    }

    .screen-container {
        height: 100%;
    }

    /* Status Bar */

    .status-bar {
        height: 25px;
        background: #004e45;
        color: #fff;
        font-size: 14px;
        padding: 0 8px;
    }

    .status-bar:after {
        content: "";
        display: table;
        clear: both;
    }

    .status-bar div {
        float: right;
        position: relative;
        top: 50%;
        transform: translateY(-50%);
        margin: 0 0 0 8px;
        font-weight: 600;
    }

    /* Chat */

    .chat {
        height: calc(100% - 69px);
    }

    .chat-container {
        height: 100%;
    }

    /* User Bar */

    .user-bar {
        height: 55px;
        background: #005e54;
        color: #fff;
        padding: 0 8px;
        font-size: 24px;
        position: relative;
        z-index: 1;
    }

    .user-bar:after {
        content: "";
        display: table;
        clear: both;
    }

    .user-bar div {
        float: left;
        transform: translateY(-50%);
        position: relative;
        top: 50%;
    }

    .user-bar .actions {
        float: right;
        margin: 5px 0 0 10px;
    }

    .user-bar .actions img {
        height:28px;
    }
    .user-bar .actions.more {
        margin: 0 12px 0 20px;
    }

    .user-bar .actions.attachment {
        margin: 0 0 0 20px;
    }

    .user-bar .actions.attachment i {
        display: block;
        /*   transform: rotate(-45deg); */
    }

    .user-bar .avatar {
        margin: 0 0 0 5px;
        width: 36px;
        height: 36px;
    }

    .user-bar .avatar img {
        border-radius: 50%;
        box-shadow: 0 1px 0 rgba(255, 255, 255, 0.1);
        display: block;
        width: 100%;
    }

    .user-bar .name {
        font-size: 17px;
        font-weight: 600;
        text-overflow: ellipsis;
        letter-spacing: 0.3px;
        margin: 0 0 0 8px;
        overflow: hidden;
        white-space: nowrap;
        width: 110px;
    }

    .user-bar .status {
        display: block;
        font-size: 13px;
        font-weight: 400;
        letter-spacing: 0;
    }

    /* Conversation */

    .conversation {
        height: calc(100% - 12px);
        position: relative;
        background: url("https://i.ibb.co/3s1f9Jq/default-wallpaper.png") repeat;
        z-index: 0;
    }

    .conversation ::-webkit-scrollbar {
        transition: all .5s;
        width: 5px;
        height: 1px;
        z-index: 10;
    }

    .conversation ::-webkit-scrollbar-track {
        background: transparent;
    }

    .conversation ::-webkit-scrollbar-thumb {
        background: #b3ada7;
    }

    .conversation .conversation-container {
        height: calc(100% - 68px);
        box-shadow: inset 0 10px 10px -10px #000000;
        overflow-x: hidden;
        padding: 0 16px;
        margin-bottom: 19px;
    }

    .conversation .conversation-container:after {
        content: "";
        display: table;
        clear: both;
    }

    /* Messages */

    .message {
        color: #000;
        clear: both;
        line-height: 18px;
        font-size: 15px;
        padding: 8px;
        position: relative;
        margin: 4px 0;
        max-width: 85%;
        word-wrap: break-word;
        z-index: 1000;
    }

    .message:after {
        position: absolute;
        content: "";
        width: 0;
        height: 0;
        border-style: solid;
    }

    .metadata {
        display: inline-block;
        float: right;
        padding: 0 0 0 7px;
        position: relative;
        bottom: -4px;
    }

    .metadata .time {
        color: rgba(0, 0, 0, .45);
        font-size: 11px;
        display: inline-block;
    }

    .metadata .tick {
        display: inline-block;
        margin-left: 2px;
        position: relative;
        top: 4px;
        height: 16px;
        width: 16px;
    }

    .metadata .tick svg {
        position: absolute;
        transition: .5s ease-in-out;
    }

    .metadata .tick svg:first-child {
        -webkit-backface-visibility: hidden;
        backface-visibility: hidden;
        -webkit-transform: perspective(800px) rotateY(180deg);
        transform: perspective(800px) rotateY(180deg);
    }

    .metadata .tick svg:last-child {
        -webkit-backface-visibility: hidden;
        backface-visibility: hidden;
        -webkit-transform: perspective(800px) rotateY(0deg);
        transform: perspective(800px) rotateY(0deg);
    }

    .metadata .tick-animation svg:first-child {
        -webkit-transform: perspective(800px) rotateY(0);
        transform: perspective(800px) rotateY(0);
    }

    .metadata .tick-animation svg:last-child {
        -webkit-transform: perspective(800px) rotateY(-179.9deg);
        transform: perspective(800px) rotateY(-179.9deg);
    }

    .message:first-child {
        margin: 4px 0 8px;
    }

    .message.received {
        background: lightskyblue;
        border-radius: 0px 5px 5px 5px;
        float: left;
    }

    .message.received .metadata {
        padding: 0 0 0 16px;
    }

    .message.received:after {
        border-width: 0px 10px 10px 0;
        border-color: transparent lightskyblue transparent transparent;
        top: 0;
        left: -10px;
    }

    .message.sent {
        background: lightskyblue;
        border-radius: 5px 0px 5px 5px;
        float: right;
    }

    .message.sent:after {
        border-width: 0px 0 10px 10px;
        border-color: transparent transparent transparent lightskyblue;
        top: 0;
        right: -10px;
    }

    /* Compose */

    .conversation-compose {
        display: flex;
        flex-direction: row;
        align-items: flex-end;
        overflow: hidden;
        height: 40px;
        width: 100%;
        z-index: 2;
    }

    .conversation-compose div,
    .conversation-compose input {
        background: #fff;
        height: 100%;
    }

    .conversation-compose .emoji {
        display: flex;
        align-items: center;
        justify-content: center;
        background: white;
        border-radius: 50% 0 0 50%;
        flex: 0 0 auto;
        margin-left: 8px;
        width: 38px;
        height: 38px;
    }

    .conversation-compose .input-msg {
        border: 0;
        flex: 1 1 auto;
        font-size: 14px;
        margin: 0;
        outline: none;
        min-width: 50px;
        height: 36px;
    }

    .conversation-compose .photo {
        flex: 0 0 auto;
        border-radius: 0 30px 30px 0;
        text-align: center;
        width: auto;
        display: flex;
        padding-right: 6px;
        height: 38px;
    }

    .conversation-compose .photo img {
        display: block;
        color: #7d8488;
        font-size: 24px;
        transform: translate(-50%, -50%);
        position: relative;
        top: 50%;
        margin-left: 10px;
    }


    .conversation-compose .send {
        background: transparent;
        border: 0;
        cursor: pointer;
        flex: 0 0 auto;
        margin-right: 8px;
        padding: 0;
        position: relative;
        outline: none;
        margin-left: .5rem;
    }

    .conversation-compose .send .circle {
        background: #008a7c;
        border-radius: 50%;
        color: #fff;
        position: relative;
        width: 38px;
        height: 38px;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .conversation-compose .send .circle i {
        font-size: 24px;
        margin-left: 1px;
    }

    /* Small Screens */

    @media (max-width: 768px) {
        .marvel-device.nexus5 {
            border-radius: 0;
            flex: none;
            padding: 0;
            max-width: none;
            overflow: hidden;
            height: 100%;
            width: 100%;
        }

        .marvel-device > .screen .chat {
            visibility: visible;
        }

        .marvel-device {
            visibility: hidden;
        }

        .marvel-device .status-bar {
            display: none;
        }

        .screen-container {
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
        }

        .conversation {
            height: calc(100vh - 55px);
        }
        .conversation .conversation-container {
            height: calc(100vh - 120px);
        }
    }

</style>