<template>
    <v-container style="height: 100%; position: relative; width: 100%" align="center">
        <v-row>
            <v-col cols="12">
                <h2>Tu perfil</h2>
            </v-col>
        </v-row>
        <PublicProfile>

        </PublicProfile>

        <v-row style="height: 100%">
            <v-col cols="12"
                   sm="6" offset-sm="3"
                   md="4" offset-md="4"
                   style="height: 100%">

                <v-container class="bottom-centered-buttons">
                    <v-btn block color="primary" @click="wantToLogout">Cerrar sesión</v-btn>
                </v-container>
            </v-col>
        </v-row>

        <v-dialog
                v-model="dialog"
        >
            <v-card>
                <br>
                <v-card-text align="justify" style="color: black; padding-bottom: 0px;">
                    <h3 style="margin-bottom: 10px">Va a salir de su cuenta</h3>
                    <p style="margin-bottom: 7px">¿Está seguro de que quiere cerrar sesión?</p>
                </v-card-text>

                <v-card-actions style="padding-bottom: 15px">
                    <v-spacer></v-spacer>
                    <v-btn color="primary" @click="dialog = false">Cancelar</v-btn>
                    <v-btn color="primary" @click="logout">Confirmar</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </v-container>

</template>

<script>
    import PublicProfile from "./PublicProfile";
    import * as firebase from "firebase";

    export default {
        name: "ProfileHome",
        data: ()=> ({
            dialog: false,
        }),
        methods:{
            wantToLogout(){
                let self = this;
                self.dialog = true;
            },
            logout(){
                let self = this;
                self.dialog = false;

                firebase.auth().signOut()
                    .then(()=>{
                        self.$router.push({name:'home'})
                    })
                    .catch(err=>{
                        console.log(err)
                    })
            }
        },
        components: {PublicProfile}
    }
</script>

<style scoped>
</style>